import React from 'react';
import s from './FilterInput.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { contactsSelectors, contactsReducer } from '../../redux/contacts';

const FilterInput = () => {
  const dispatch = useDispatch();
  const filterValue = useSelector(contactsSelectors.getFilter);
  const changeFilter = e => {
    dispatch(contactsReducer.filter(e.currentTarget.value));
  };

  return (
    <div className={s.filter}>
      <label className={s.label}>
        Find contacts by name
        <input
          className={s.input}
          type="text"
          name="filter"
          value={filterValue}
          onChange={changeFilter}
        />
      </label>
    </div>
  );
};

export default FilterInput;
